<template>
  <section class="articles">
    <div class="article" v-for="(item, index) in articles" :key="index" @click="link(item,  index)">
      <h1 class="title">{{  item.title  }}</h1>
      <p class="date">{{  item.createdTime  }} - by {{  item.author  }}</p>
      <p class="desc">{{  item.preview  }}</p>
    </div>
  </section>
</template>
<script>
import Articles from '@/articles/articles.json'

export default {
  data() {
    return {
      articles: []
    }
  },
  created() {
    this.getData()
  },
  methods: {
    getData() {
      // 倒序显示 最新的在最前面
      this.articles = Articles.reverse()
    },
    link(item, index) {
      this.$router.push({
        path: `/articles/${this.articles.length - 1 - index}`
      })
    }
  }
}
</script>
<style lang="less" scoped>
.articles {
  padding: 0.267rem 2.667rem;

  .article {
    padding: 0.267rem;
    margin-bottom: 0.133rem;
    border-radius: 0.267rem;
    box-shadow: 0 1px 0.053rem rgba(0, 0, 0, 0.1);
    text-align: left;
    cursor: pointer;


    .date {
      font-size: 0.5rem;
      color: #ccc;
    }

    .desc {
      font-size: 0.6rem;
    }
  }
}

@media (max-width: 10.667rem) {
  .articles {
    padding: 0.267rem 0.320rem;
  }
}
</style>



